import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { Card, Row, Col, message, Checkbox } from 'antd';
import { getPracticeTypes, deletePracticeType } from 'services/practiceType';

class TrainingTypePage extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deletePracticeType(id);
    message.success('Xóa thành công');
    this.setState({ reload: this.state.reload + 1 });
  };

  render() {
    const columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên loại RL',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Điểm tối đa',
        dataIndex: 'grade',
        key: 'grade',
      },
      {
        title: 'Tính tổng điểm',
        dataIndex: 'isTotal',
        key: 'isTotal',
        render: value => <Checkbox checked={value} />,
      },
      {
        title: 'Điểm học tập',
        dataIndex: 'isStudy',
        key: 'isStudy',
        render: value => <Checkbox checked={value} />,
      },
      {
        title: 'Hiển thị',
        dataIndex: 'isShown',
        key: 'isShown',
        render: value => <Checkbox checked={value} />,
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#/training/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Loại rèn luyện">
        <CartHeadAction>
          <BtnAddNew href="#/training/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getPracticeTypes}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingTypePage />
  </Page>
);



